import React, { useEffect, useState } from 'react';
import style from './Skills.module.css';

const Skills = ({ percent, name }) => {
  const [loadPersent, setLoadPercent] = useState(0);
  useEffect(() => {
    if (loadPersent < percent) {
      setTimeout(() => setLoadPercent((loadPersent) => (loadPersent += 4)), 50);
    }
  }, [loadPersent]);
  return (
    <div className={style.skills_name}>
      <h4>{name}</h4>
      <div className={style.progress}>
        <div
          style={{
            height: '10px',
            width: `${loadPersent}%`,
            backgroundColor: '#f5f5f5',
            borderRadius: '100px',
            boxShadow: '0 4px 10px 0px #f5f5f5',
            transition: 'width 0.5s',
            textAlign: 'center',
          }}
        ></div>
        <div>{loadPersent}%</div>
      </div>
    </div>
  );
};

export default Skills;
